@import "../../data/styles.css";

.homepage-logo-container {
	display: flex;
	justify-content: left;
	align-items: center;
	padding-top: 130px;
}

.homepage-container {
	padding-top: 25px;
}

.homepage-first-area {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.homepage-first-area-left-side {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.homepage-title {
	width: 85% !important;
}

.homepage-subtitle {
	width: 85% !important;
}

.homepage-first-area-right-side {
	display: flex;
	align-items: center;
}

.homepage-image-container {
	width: 370px;
	height: 370px;
}

.homepage-image-wrapper {
	overflow: hidden;
	border-radius: 10%;
	transform: rotate(3deg);
}

.homepage-image-wrapper img {
	width: 100%;
}

.homepage-socials {
	display: flex;
	justify-content: left;
	align-items: center;
	padding-top: 35px;
	gap: 8px;
}

.homepage-social-icon {
	padding: 12px;
	font-size: 20px;
	transition: all 0.3s ease-in-out;
	color: var(--secondary-color);
	border-radius: 10px;
	display: flex;
	align-items: center;
	justify-content: center;
	background: #f9fafb;
	border: 1px solid #f0f0f0;
}

.homepage-social-icon:hover {
	color: white;
	background: linear-gradient(135deg, var(--link-color) 0%, #0d9488 100%);
	border-color: var(--link-color);
	transform: translateY(-3px);
	box-shadow: 0 4px 12px rgba(20, 184, 166, 0.2);
}

.homepage-after-title {
	display: flex;
}

.homepage-projects {
	flex-basis: 300px;
	padding-top: 40px;
	margin-bottom: 20px;
}

.see-more-projects-container {
	display: flex;
	justify-content: flex-start;
	padding-top: 10px;
	padding-bottom: 20px;
}

.see {
	display: inline-block;
	padding: 12px 24px;
	background: linear-gradient(135deg, var(--link-color) 0%, #0d9488 100%);
	color: white;
	border-radius: 10px;
	font-size: 13px;
	font-weight: 700;
	cursor: pointer;
	transition: all 0.3s ease-in-out;
	border: 1px solid var(--link-color);
	letter-spacing: 0.3px;
}

.see:hover {
	transform: translateY(-2px);
	box-shadow: 0 8px 20px rgba(20, 184, 166, 0.3);
	background: linear-gradient(135deg, #0d9488 0%, var(--link-color) 100%);
}

.homepage-tech-section {
	padding-top: 40px;
	padding-bottom: 30px;
}

.homepage-articles {
	flex-basis: 900px;
	padding-top: 30px;
	margin-left: -30px;
}

.homepage-article {
	padding-bottom: 5px;
}

.homepage-works {
	flex-basis: 600px;
	padding-top: 30px;
}

@media (max-width: 1024px) {
	.homepage-first-area {
		flex-direction: column;
	}

	.homepage-first-area-left-side {
		display: flex;
		flex-direction: column; /* Changed: set direction to column */
		align-items: flex-start;
		width: 100%;
		order: 2;
	}

	.homepage-title {
		width: 100% !important;
		margin-top: 20px;
		order: 2;
	}

	.homepage-subtitle {
		width: 100% !important;
		margin-top: 10px;
		order: 3;
	}

	.homepage-first-area-right-side {
		width: 100%;
		order: 1;
	}

	.homepage-image-container {
		padding-top: 50px;
		padding-bottom: 50px;
		margin-left: 10px;
		order: 1;
	}

	.homepage-after-title {
		flex-direction: column;
	}

	.homepage-projects {
		flex-basis: auto;
	}

	.homepage-articles {
		flex-basis: auto;
	}

	.homepage-works {
		flex-basis: auto;
		margin-left: -25px;
		margin-right: -25px;
	}
}
